import DdzBaseView from "./DdzBaseView";
import DDZTimeAutoRun from "../component/DDZTimeAutoRun";

/**
 * 匹配倒计时view
 */
export default class DdzMatchPlayerView extends DdzBaseView {
    private spotList: cc.Node[] = [];
    private delay: number = 0.1;
    private interval: NodeJS.Timeout;
    private timeRun: DDZTimeAutoRun;

    constructor(node: cc.Node) {
        super();
        this.setNode(node);
    }

    protected initView() {
        this.spotList = [];
        for (let i = 1; i <= 9; i++) {
            let node = this.getChild('action/' + i.toString());
            this.spotList.push(node);
            node.active = true;
        }
        this.timeRun = <DDZTimeAutoRun>this.getComponent('timeLbl', DDZTimeAutoRun);
        this.timeRun.node.active = false;
    }

    protected onOpen() {
        this.interval = setInterval(() => {
            this.startAnim();
        }, 1500);
        this.startAnim();
    }

    public startAnim() {
        for (let i = 0; i <= this.spotList.length - 1; i++) {
            let tween = Game.Tween.get(this.spotList[i]);
            tween.delay(i * this.delay)
                .by(0.2, { position: cc.v2(0, 10) }, cc.easeIn(1))
                .by(0.2, { position: cc.v2(0, -10) }, cc.easeOut(1))
                .start();
        }
    }

    public resetAnim() {
        this.spotList.forEach(node => {
            node.y = 0;
        });
    }

    protected onClose() {
        clearInterval(this.interval);
        this.resetAnim();
        this.timeRun.node.active = false;
    }

    public clearByRound() {
        this.active = false;
    }

    public setTimeRunConfig(leftTime: number, callback: Function, target: any) {
        this.timeRun.node.active = true;
        this.timeRun.setTimer(leftTime, callback, target);
    }
}